<template>
  <div >
    <LoHeader class="lo_header"  @query="queryData" :FilterItems="FilterItems" title="流水"></LoHeader>
    <div class="lo_content_box">
        <mu-load-more class="lo_content"  :refreshing="refreshing" :loading="loading" style="width: 100%;"
                      @refresh="refresh" @load="load">
          <template v-for="item in tableData">
            <div>
              <div class="container">
                <div class="desc">
                  <div class="title">
                    <div class="title_1">{{ item.UserName }}</div>
                  </div>
                  <div class="priceDiv">
                    <div class="price">
                      {{item.StrType}}:{{item.Gold}}
                    </div>
                  </div>
                  <div class="kc">
                    <span>剩余：{{item.CurUserGold}}</span>
                    <span class="xiaol">时间：{{item.CreatedAt}}</span>
                  </div>
                </div>
              </div>
            </div>
          </template>
        </mu-load-more>
    </div>
  </div>
</template>

<script>
import LoHeader from "../../components/LoHeader";
import {GetAccountListFirstId} from '../../api/Finance';

export default {
  name: "AccountList",
  components: {
    LoHeader
  },
  data () {
    return {
      num: 10,
      refreshing: false,
      loading: false,
      FilterItems:[
        {label:'用户名:',Value:'',type:"text",placeholder:"用户名",icon:"",Name:"userName"},
        {label:'账目类型:',Value:0,type:"select",placeholder:"账目类型",icon:"",Name:"accountType",options:this.Config.AccountTypesOptions},
        {label:'开始时间:',Value:'',type:"date",placeholder:"开始时间",icon:"",Name:"beginDay"},
        {label:'结束时间:',Value:'',type:"date",placeholder:"结束时间",icon:"",Name:"endDay"},
      ],


      PageTotal:0,
      tableData: [],
      GroupData:{},
      query: {
        userName:'',
        accountType:0,
        beginDay: '',
        endDay: '',
        pageIndex: 1,
        pageSize: 10,
        GameType:0,
        FirstId:0,
      },
    }
  },
  methods: {
    refresh () {
      this.refreshing = true;
      // this.$refs.container.scrollTop = 0;
      this.query.pageIndex = 1;
      this.query.FirstId = 0;

      this.getData((v)=>{
        if (v==true){
          this.query.pageIndex = this.query.pageIndex+1
        } else {
          this.$toast.error(msg);
        }
        this.refreshing = false;
      });
    },
    load() {
      this.loading = true;
      this.getData((v)=>{
        if (v==true){
          this.query.pageIndex = this.query.pageIndex+1
        } else {
          this.$toast.error(msg);
        }
        this.loading = false;
      });

    },
    getData(fun){
      GetAccountListFirstId(this.query).then(response => {
        const { obj, code, msg } = response;
        if (code == 200) {
          if (this.query.FirstId==0) {
            this.tableData = obj.ListData;
          } else {
            this.tableData = this.tableData.concat(obj.ListData);
          }
          this.GroupData = obj.GroupData;
          this.PageTotal = obj.PageTotal;
          this.query.FirstId = obj.GroupData.FirstId;
          fun(true);
          resolve();
        } else {
          this.$toast.error(msg);
          fun(false);
        }
      }).catch(error => {
        fun(false);
      });
    },
    queryData(filterItems){
      this.query.pageIndex = 1;
      this.query.FirstId = 0;
      this.query.userName = '';
      this.query.accountType =0;
      this.query.beginDay = '';
      this.query.endDay = '';

      for (let key in filterItems) {
        if (filterItems[key].Name === 'userName') {
          this.query.userName = filterItems[key].Value;
        } else if (filterItems[key].Name === 'accountType') {
          this.query.accountType = filterItems[key].Value;
        } else if (filterItems[key].Name === 'beginDay') {
          this.query.beginDay = filterItems[key].Value;
        }else if (filterItems[key].Name === 'endDay') {
          this.query.endDay = filterItems[key].Value;
        }
      }

      this.getData((v)=>{
        if (v==true){
          this.query.pageIndex = this.query.pageIndex+1
        }
      });
    }
  },

  created() {
    this.query.pageIndex = 1;
    this.query.FirstId = 0;
    this.getData((v)=>{
      if (v==true){
        this.query.pageIndex = this.query.pageIndex+1
      }
    });
  },
}
</script>

<style scoped>
@import url('../../assets/css/lottery_num.css');
@import url('../../assets/css/main.css');

.container {
  width: 100%;
  display: flex;
  flex-direction: row;
  border-bottom: 1px solid #F2F2F2;
  justify-content: space-between;
  padding: 5px 11px;
  margin: 0 0 10px;
  background: #fff;
}
.desc {
  color: #8B8B8B;
  padding: 2px;
  text-align: left;
  font-size: 12px;
  width: 100%;
}
.title {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  justify-content: space-between;
}
.title_1 {
  font-weight: bold;
  color: #333;
  overflow: hidden;
  font-size: 14px;
}
.price {
  color: red;
  font-size: 16px;
  font-weight: 900;
}
.fsid {
  color: #8B8B8B;
}

.priceDiv {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  justify-content: space-between;
}
.xiaol {

  color: #8B8B8B;
  color: red;
}
.kc {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.btn_01 {
  border: 1px solid #8B8B8B;
  border-radius: 3px;
  text-align: center;
  margin-left: 10px;
  padding: 3px 8px;
}
.btn {
  display: flex;
  justify-content: flex-end;
}
.main {
  margin-top: 8px;
  width: 100%;
  /*margin-top: 1.4rem;*/
  margin-bottom: 1.4rem;
}


</style>



